.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}


.HomePage {
    width: 100%;
}
.Header {
    width: 100%;
    padding: 16px 0;
    background-color: #2b244d;
    color: white;
    text-align: center;
    font-size: 18px;
    font-family: 'Fira Sans', sans-serif;
}
.Main {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 100vh;
    background: linear-gradient(#e778bf, #9e6bdb, #6d62ed)
}

.weather-container {
    width: 60%;
    height: 80vh;
    border-radius: 10px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.background {
    border-radius: 10px 10px 0 0;
    width: 100%;
    height: 46%;
    background: url(/static/media/tbilisi-night.11585ac5.jpg);
}

.temperature {
    border-radius: 10px 10px 0 0;
    width: 100%;
    height: 100%;
    background: rgba(48, 35, 125, 0.7);
    display: -webkit-flex;
    display: flex;
}

.index {
    display: -webkit-flex;
    display: flex;
    width: 50%;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    font-family: 'Fira Sans', sans-serif;
}

.index div {
    text-align: left;
}

.index h1 {
font-size: 4rem;
opacity: 1.0;
font-weight: normal;
margin: 0;
}

.clouds {
    letter-spacing: 4px;
    opacity: 0.8;
    margin: 0;
    font-size: 22px;
}

.white-line {
    width: 50px;
    height: 1px;
    background: #fff;
}

.black-line {
    width: 100%;
    height: 4%;
    background: linear-gradient(#120e48, #242578);
}

.city {
    font-size: 48px !important;
}

.weekdays {
    border-radius: 0 0 10px 10px;
    width: 100%;
    height: 50%;
    background-color: #f7f5ff;
}

.parameters {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

}

.parameters p {
    opacity: 0.6;
    margin: 0;
    font-size: 16px;
    padding: 6px;
}

.row {
    width: 70%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 30px;
}

.vertical-line {
    height: 40px;
    width: 1px;
    background: #fff;
    opacity: 0.5;
}
.forecast {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 100%;
    height: 100%;
    background-color: rgb(223, 219, 219);
