.forecast {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    background-color: rgb(223, 219, 219);
    align-items: center;
    justify-content: space-around;
}

.weather-card {
    width: 16%;
    height: 70%;
}

.card {
    justify-content: space-between;
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 100%;
    align-items: center;
    padding: 8px 0 24px 0;
    background: #fff;
    border-radius: 8px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.card p {
    margin: 6px;
}

.clouds-img {
    width: 50%;
    height: auto;
    object-fit: contain;
    background: transparent;
}
.temp {
    width: 76%;
    display: flex;
    flex-direction: row;
    font-size: 22px;
    color: rgb(68, 63, 63);
    justify-content: space-between;
}